@import "../../variables";

.contact {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.contact-h1 {
  font-size: 34px;
}

.icon {
  height: 50px;
  width: 50px;
  margin-bottom: 40px;
}

.contact-wrapper {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  min-width: 100%;
}

.contact-main {
  display: flex;
  flex-direction: column;
  align-items: center;
  h1 {
    margin-bottom: 50px;
  }
}

.contact-div {
  display: flex;
}

.details {
  display: flex;
  flex-direction: column;
  margin-left: 50px;
  .headers {
    margin: 0;
    color: $sub-text;
    font-family: MomcakeThin;
  }
  .email-link,
  .contact-content {
    color: $main-text;
    font-size: 24px;
    margin: 0;
  }
  .email-link:hover {
    color: $highlighter;
  }
}

.send-message {
  text-align: center;
  text-decoration: underline;
  font-size: 32px;
  font-family: monospace;
  letter-spacing: 5px;
}

.rainbow-animate {
  background: linear-gradient(
    to right,
    #6666ff,
    #0099ff,
    #00ff00,
    #ff3399,
    #6666ff
  );
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  animation: rainbow_animation 6s ease-in-out infinite;
  background-size: 400% 100%;
}

@keyframes rainbow_animation {
  0%,
  100% {
    background-position: 0 0;
  }

  50% {
    background-position: 100% 0;
  }
}

.i-twitter {
  -webkit-animation: i-twitter 1s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  animation: i-twitter 1s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}

@-webkit-keyframes i-twitter {
  0% {
    -webkit-transform: translateY(-1000px);
    transform: translateY(-1000px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes i-twitter {
  0% {
    -webkit-transform: translateY(-1000px);
    transform: translateY(-1000px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    opacity: 1;
  }
}

.i-linkedin {
  -webkit-animation: i-linkedin 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.5s
    both;
  animation: i-linkedin 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.5s both;
