.i-message {
  -webkit-animation: i-message 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) 1s both;
  animation: i-message 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) 1s both;
